*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

section,
footer {
  min-height: 100vh;
  display: flex;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  background: #000;
}

header,
section,
footer {
  z-index: 1;
}

input,
textarea,
button {
  outline: none;
}

button {
  cursor: pointer;
}

canvas {
  display: flex;
  flex: 1 1 auto;
  max-width: 100vw;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  pointer-events: none;
}

.container {
  max-width: 1560px;
  width: 100%;
  flex: 1 1 auto;
  margin: 0 auto;
  padding: 0 20px;
}

._animBtn {
  position: relative;
}

._animBtn::after {
  content: '';
  position: absolute;
  top: 0;
  left: auto;
  right: 0;
  height: 100%;
  width: 0;
  transition: all .3s;
  background: #F4CF49;
  z-index: -1;
}

._animBtn:hover::after {
  right: auto;
  left: 0;
  width: 100%;
}

._scrollBan {
  overflow: hidden;
}

.clr-mark {
  color: #A0F4FF;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  text-decoration: none;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
header,
hgroup,
menu,
nav {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after {
  content: '';
  content: none;
}

q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.preloader {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
  color: #fff;
  font-family: "Tourney", sans-serif;
  font-weight: 900;
  font-size: 100px;
  z-index: 10000;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.51, 0.36, 0.73, 0.61);
}

.preloader__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 90px 20px;
}

.preloader__progressbar {
  position: relative;
  max-width: 700px;
  width: 100%;
  height: 7px;
  margin-top: 40px;
  background: #464646;
  transition: all .3s;
}

.preloader__progressbar-line {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: white;
  transition: all .3s;
}

.preloader__progressbar-text {
  display: block;
  position: absolute;
  top: 10px;
  right: 0;
  font-size: 16px;
  transition: all .3s;
}

.preloader__title {
  display: block;
  max-width: 100%;
  font-family: "Tourney", sans-serif;
  font-size: 100px;
  line-height: 100px;
  letter-spacing: 2px;
  text-align: center;
}

.preloader__img {
  max-width: 600px;
}

._load {
  overflow: hidden;
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 112px;
  display: flex;
  align-items: center;
  transform: translateX(-50%);
  background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.5) 100%);
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px 0 20px;
  flex: 1 1 auto;
}

.logo {
  width: 25px;
  height: 39px;
}

.logo__img {
  width: 100%;
  height: 100%;
  object-position: center;
}

.menu {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 30px;
  font-family: "Tourney", sans-serif;
  font-size: 20px;
  line-height: 112px;
  font-weight: 900;
  position: relative;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  color: #fff;
}

.burger__btn {
  display: none;
  position: relative;
  width: 40px;
  height: 20px;
  transition: all .5s;
  cursor: pointer;
}

.burger__btn::after {
  content: '';
  position: absolute;
  width: 50%;
  height: 2px;
  bottom: 0;
  right: 0;
  background: #fff;
  transition: all .3s;
}

.burger__btn span {
  position: absolute;
  width: 70%;
  height: 2px;
  top: 50%;
  transform: translate(0, -50%);
  right: 0;
  background: #fff;
  transition: all .3s;
}

.burger__btn::before {
  content: '';
  position: absolute;
  width: 90%;
  height: 2px;
  top: 0;
  right: 0;
  background: #fff;
  transition: all .3s;
}

._isActive {
  width: 35px;
}

._isActive span {
  display: none;
}

._isActive::after {
  width: 100%;
  bottom: 50%;
  transform: translate(0, 50%) rotate(-45deg);
}

._isActive::before {
  width: 100%;
  top: 50%;
  transform: translate(0, -50%) rotate(45deg);
}

.menu__item::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: auto;
  right: 0;
  width: 0;
  height: 4px;
  background: #B937B2;
  transition: all .3s;
}

.menu__item:hover::before {
  right: auto;
  left: 0;
  width: 100%;
}

.present-section {
  padding: 90px 0;
}

.present__content {
  width: inherit;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
}

.present__title {
  font-family: "Tourney", sans-serif;
  font-size: 64px;
  line-height: 64px;
  color: #fff;
  text-align: center;
}

.present__text {
  font-family: "Tourney", sans-serif;
  font-size: 35px;
  line-height: 45px;
  font-weight: 900;
  color: #fff;
  text-align: center;
  margin-top: 30px;
}

.btn__get {
  font: 900 28px/45px "Tourney", sans-serif;
  margin-top: 140px;
  width: 320px;
  height: 100px;
  border: 3px solid #F4CF49;
  background: transparent;
  color: #fff;
  text-align: center;
  position: relative;
  z-index: 0;
  transition: all .3s;
}

.btn__get:hover {
  color: #000;
}

.scroll__icon {
  position: absolute;
  width: 45px;
  height: 60px;
  bottom: 30px;
  left: 50%;
  width: 45px;
  height: 60px;
  transform: translateX(-50%);
  animation: moveScroll 1s ease 0s infinite normal none;
}

@keyframes moveScroll {
  0% {
    transform: translate(-50%, 0);
  }

  50% {
    transform: translate(-50%, 10px);
  }

  100% {
    transform: translate(-50%, 0);
  }
}

.about-section {
  position: relative;
  width: inherit;
  background: #fff;
  padding: 90px 0;
}

.about__title {
  position: absolute;
  font-family: "Tourney", sans-serif;
  font-weight: normal;
  max-width: 520px;
  font-size: 250px;
  line-height: 300px;
  text-align: left;
  top: 50%;
  left: -16px;
  transform: translate(0, -50%);
  pointer-events: none;
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.about__info-photos {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.about__content {
  height: 100%;
}

.photo {
  position: relative;
  width: auto;
  height: auto;
  overflow: hidden;
}

.photo__overlay {
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  text-align: center;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9) 10%, rgba(185, 55, 178, 0.4) 100%);
  transition: all .3s;
}

.photo:hover > .photo__overlay {
  left: 0;
  top: 0;
}

.photo:hover > .photo__img {
  transform: scale(1.1);
}

.photo__overlay-line {
  display: block;
  width: 50px;
  height: 3px;
  background: #fff;
}

.photo__overlay-name {
  font-family: "Tourney", sans-serif;
  font-size: 24px;
  font-weight: 900;
  font-style: italic;
  color: #fff;
  margin-top: 80px;
  margin-bottom: 35px;
}

.photo__overlay-text {
  font-family: "Tourney", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 900;
  color: #fff;
  margin-top: 35px;
  text-align: center;
}

.photo__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  transition: all .3s;
}

.lottie-hint {
  position: absolute;
  top: -2%;
  left: -2%;
  z-index: 10;
  width: 120px;
  height: 120px;
  transition: .3s;
  opacity: 0.9;
}

.photo:hover > .lottie-hint {
  transition: all .3s;
  width: 0;
  height: 0;
}

.wrapper__photo {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #000;
  width: 340px;
  height: 400px;
  margin-left: 30px;
}

.wrapper__photo:first-child {
  margin-left: 0;
}

.about__info-text {
  max-width: 756px;
  margin-top: 60px;
}

.about__text {
  font: 900 28px/45px "Tourney", sans-serif;
  text-align: right;
}

.experiens-section {
  position: relative;
  width: inherit;
  overflow: hidden;
  background: #000;
  box-shadow: 0px 0px 100px 15px rgba(0, 0, 0, 0.25);
  padding: 90px 0 90px 0;
}

.experiens__content {
  height: 100%;
  padding: 0 20px 0 20px;
}

.experiens__title {
  color: #fff;
  font-family: "Tourney", sans-serif;
  font-size: 64px;
  line-height: 64px;
  font-weight: 900;
}

.experiens__text {
  font: 900 28px/45px "Tourney", sans-serif;
  color: #fff;
  margin-top: 100px;
}

.experiens__decor {
  position: absolute;
  width: 120%;
  bottom: -10%;
  left: -10%;
  pointer-events: none;
  z-index: -1;
}

.philosophy-section {
  width: inherit;
  padding: 90px 0 90px 0;
  background: #fff;
}

.philosophy__content {
  height: 100%;
  padding: 0 20px;
}

.philosophy__title {
  font-family: "Tourney", sans-serif;
  font-size: 100px;
  line-height: 100px;
  font-weight: 900;
  color: #000;
  text-align: center;
}

.philosophy__slide-text {
  font: 900 28px/45px "Tourney", sans-serif;
  color: #000;
}

.philosophy__slider {
  padding: 100px 0 160px 0 !important;
}

.philosophy__slide {
  position: relative;
  padding: 200px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.philosophy__slide-text {
  text-align: center;
  max-width: 555px;
}

.philosophy__pagination {
  left: 0 !important;
  width: 100% !important;
  height: 0 !important;
  bottom: 39px !important;
  background: transparent !important;
  text-align: center;
}

.philosophy__slide-prev,
.philosophy__slide-next {
  top: inherit !important;
  bottom: 0px !important;
  width: 56px !important;
  height: 56px !important;
}

.philosophy__slide-prev::after,
.philosophy__slide-next::after {
  display: none !important;
}

.philosophy__slide-prev:hover path,
.philosophy__slide-next:hover path {
  fill: #B937B2;
}

.philosophy__slide-prev svg path {
  transition: all .3s;
}

.philosophy__slide-next svg path {
  transition: all .3s;
}

.philosophy__slide-prev {
  left: 35% !important;
}

.philosophy__slide-next {
  right: 35% !important;
}

.swiper-pagination-bullet {
  width: 50px !important;
  height: 2px !important;
  border-radius: 0 !important;
  margin: 0 5px !important;
}

.swiper-pagination-bullet-active {
  background: #B937B2 !important;
}

.slide__border {
  position: absolute;
}

.slide__border-left {
  top: 0;
  left: 0;
  height: 100%;
}

.slide__border-right {
  right: 0;
  top: 0;
  height: 100%;
}

.slide__border-top {
  left: 0;
  top: 0;
  width: 100%;
}

.slide__border-bottom {
  left: 0;
  bottom: 0;
  width: 100%;
}

.technologies-section {
  width: inherit;
  padding: 90px 0 90px 0;
  background: #000;
  box-shadow: 0px 0px 100px 15px rgba(0, 0, 0, 0.25);
}

.technologies__title {
  font-family: "Tourney", sans-serif;
  font-size: 64px;
  line-height: 64px;
  color: #fff;
  text-align: left;
  margin-bottom: 40px;
}

.technologies__container.swiper-container {
  padding: 0 20px;
  overflow-x: auto;
}

.technologies__cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.technologies__card {
  position: relative;
  max-width: 750px;
  flex: 1 1 auto;
  border: 2px solid #fff;
  padding: 60px 40px;
  margin: 10px 5px;
  overflow: hidden;
  transition: all .3s;
}

.technologies__card-title {
  text-align: center;
  margin-bottom: 60px;
}

.technologies__card-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  flex: 1 1 auto;
}

.technologies__card-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  transition: all .3s;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9) 20%, rgba(185, 55, 178, 0.6) 136%);
}

.technologies__card:hover .technologies__card-overlay {
  top: 0;
}

.technologies__card:focus .technologies__card-overlay {
  top: 0;
}

.technologies__card:hover .technologies__card-icons .card-icon-wrap .card-icon-name {
  top: -50px;
}

.swiper-container-vertical > .swiper-scrollbar {
  background: rgba(25, 25, 25, 0.8) !important;
  width: 4px !important;
}

.swiper-scrollbar-drag {
  background: #B937B2 !important;
  opacity: 0.8 !important;
}

.technologies__card-title {
  font-family: "Tourney", sans-serif;
  font-weight: 900;
  font-size: 36px;
  line-height: 40px;
  color: #fff;
}

._lonely {
  justify-content: center;
}

.card-icon-wrap {
  position: relative;
  margin: 5px;
}

.card-icon-wrap > object {
  width: 100%;
  height: 100%;
}

.card-icon-name {
  display: block;
  position: absolute;
  top: -1000px;
  left: 50%;
  transform: translate(-50%, 0);
  transition: all .3s ease .2s;
  color: #fff;
  font-family: "Tourney", sans-serif;
  font-style: italic;
  font-weight: 900;
  font-size: 18px;
  line-height: 18px;
}

.modal {
  position: fixed;
  transform: scale(0);
  visibility: hidden;
  top: 0;
  left: 0;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.95);
  z-index: 10000;
  overflow-y: auto;
  transition: all .3s;
}

.modal__title {
  font-family: "Tourney", sans-serif;
  font-weight: 900;
  font-size: 64px;
  line-height: 64px;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
}

.modal__text {
  font: 900 28px/45px "Tourney", sans-serif;
  text-align: center;
  color: #fff;
}

.modal__container {
  max-width: 1040px;
  width: 100%;
  min-height: inherit;
  flex: 1 1 auto;
  padding: 70px 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 90px;
}

.form__item {
  position: relative;
}

.form__item-textarea {
  max-height: 250px;
  height: 100%;
  margin-top: 80px;
}

.form__email-input {
  background: transparent;
  width: 100%;
  border: none;
  border-bottom: 2px solid #fff;
  font-weight: 900;
  font: 900 28px/45px "Tourney", sans-serif;
  color: #fff;
  padding: 0 0 20px 15px;
}

.form__email-input::placeholder {
  color: #fff;
  font-style: italic;
}

.form__textarea {
  width: 100%;
  background: transparent;
  border: 2px solid #fff;
  font-weight: 900;
  font: 900 28px/45px "Tourney", sans-serif;
  color: #fff;
  padding: 5px 15px 15px 15px;
  resize: none;
  min-height: 250px;
}

.form__textarea::placeholder {
  color: #fff;
  font-style: italic;
}

.form__textarea.js-validate-error-field {
  border: 2px solid rgba(255, 0, 0, 0.4) !important;
}

.form__email-input.js-validate-error-field {
  border: none !important;
  border-bottom: 2px solid rgba(255, 0, 0, 0.4) !important;
}

.js-validate-error-label {
  position: absolute;
  bottom: -20px;
  font-family: "Tourney", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 900;
  font-style: italic;
  color: red;
  opacity: 0.5;
}

.form__batton-send {
  display: block;
  margin-top: 80px;
  width: 260px;
  height: 90px;
  border: 3px solid #F4CF49;
  background: transparent;
  color: #fff;
  font-family: "Tourney", sans-serif;
  font-weight: 900;
  font-size: 28px;
  line-height: 20px;
  text-align: center;
  transition: all .3s;
}

.form__batton-send:hover {
  color: #000;
}

.modal__close {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 25px;
  right: 25px;
  cursor: pointer;
}

.modal__close::after {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%) rotate(45deg);
  content: '';
  width: 100%;
  height: 2px;
