  opacity: 0;
  position: absolute;
  min-height: 100vh;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
  background: rgba(0, 0, 0, 0.9);
  transition: all .3s;
}

